import React from 'react';
import '../css/Incarnate.css';

import arrow from '../img/down_arrow.png';
import Drach from './incarnate_chara/Drach';
import Kayn from './incarnate_chara/Kayn.js';
import Kiyone from './incarnate_chara/Kiyone.js';
