import React from 'react';
import '../css/Kingpin.css';
import arrow from '../img/down_arrow.png';
import Brighte from './kingpin/brighte/Brighte.js';
import Laleh from './kingpin/laleh/Laleh.js';
import Lydi from './kingpin/lydi/Lydi.js';
import Nakoa from './kingpin/nakoa/Nakoa.js';
import Rin from './kingpin/rin/Rin';
import Robyn from './kingpin/robyn/Robyn.js';
import Sumina from './kingpin/sumina/Sumina';

class Kingpin_Chara extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showBrighte: false,
            showLaleh: false,
            showLydi: false,
            showNakoa: false,
            showRin: false,
            showRobyn: false,
            showSumina: false,
        };
    }

    toggleBrighte = () => {
        this.setState({
            showBrighte: true,
            showLaleh: false,
            showLydi: false,
            showNakoa: false,
            showRin: false,
            showRobyn: false,
            showSumina: false
        });
        window.scrollTo({ top: 870, behavior: 'smooth' });
    }

    toggleLaleh = () => {
        this.setState({
            showBrighte: false,
            showLaleh: true,
            showLydi: false,
            showNakoa: false,
            showRin: false,
            showRobyn: false,
            showSumina: false
        });
        window.scrollTo({ top: 870, behavior: 'smooth' });
    }

    toggleLydi = () => {
        this.setState({
            showBrighte: false,
            showLaleh: false,
            showLydi: true,
            showNakoa: false,
            showRin: false,
            showRobyn: false,
            showSumina: false
        });
        window.scrollTo({ top: 870, behavior: 'smooth' });
    }

    toggleNakoa = () => {
        this.setState({
            showBrighte: false,
            showLaleh: false,
            showLydi: false,
            showNakoa: true,
            showRin: false,
            showRobyn: false,
            showSumina: false
        });
        window.scrollTo({ top: 870, behavior: 'smooth' });
    }

    toggleRin = () => {
        this.setState({
            showBrighte: false,
            showLaleh: false,
            showLydi: false,
            showNakoa: false,
            showRin: true,
            showRobyn: false,
            showSumina: false
        });
        window.scrollTo({ top: 870, behavior: 'smooth' });
    }

    toggleRobyn = () => {
        this.setState({
            showBrighte: false,
            showLaleh: false,
            showLydi: false,
            showNakoa: false,
            showRin: false,
            showRobyn: true,
            showSumina: false
        });
        window.scrollTo({ top: 870, behavior: 'smooth' });
    }

    toggleSumina = () => {
        this.setState({
            showBrighte: false,
            showLaleh: false,
            showLydi: false,
            showNakoa: false,
            showRin: false,
            showRobyn: false,
            showSumina: true
        });
        window.scrollTo({ top: 870, behavior: 'smooth' });
    }

    render() {
        return (
            <div>
                <div className="kingpin_selection">
                    <div className="character">
                        <p className="brighteActive">BRIGHTE AVER</p>
                        <div className={this.state.showLaleh || this.state.showLydi || this.state.showNakoa || this.state.showRin || this.state.showRobyn || this.state.showSumina
                            ? "brighteCardInactive" : "brighteCardIcon"}
                            onClick={this.toggleBrighte}>
                            <p className="tagline">the blessed storm weaver</p>
                        </div>
                    </div>
                    <div className="character">
                        <p className="lalehActive">LALEH MILAAN</p>
                        <div className={this.state.showBrighte || this.state.showLydi || this.state.showNakoa || this.state.showRin || this.state.showRobyn || this.state.showSumina
                            ? "lalehCardInactive" : "lalehCardIcon"}
                            onClick={this.toggleLaleh}>
                            <p className="tagline">the wolf in sheep's clothing</p>
                        </div>
                    </div>
                    <div className="character">
                        <p className="lydiActive">LYDI VALERIM</p>
                        <div className={this.state.showBrighte || this.state.showLaleh || this.state.showNakoa || this.state.showRin || this.state.showRobyn || this.state.showSumina
                            ? "lydiCardInactive" : "lydiCardIcon"}
                            onClick={this.toggleLydi}>
                            <p className="tagline">the crown of a weighted heart</p>
                        </div>
                    </div>
                    <div className="character">
                        <p className="nakoaActive">NAKOA KILLIUS</p>
                        <div className={this.state.showBrighte || this.state.showLaleh || this.state.showLydi || this.state.showRin || this.state.showRobyn || this.state.showSumina
                            ? "nakoaCardInactive" : "nakoaCardIcon"}
                            onClick={this.toggleNakoa}>
                            <p className="tagline">the boy king</p>
                        </div>
                    </div>
                    <div className="character">
                        <p className="rinActive">RIN XINLEI</p>
                        <div className={this.state.showBrighte || this.state.showLaleh || this.state.showLydi || this.state.showNakoa || this.state.showRobyn || this.state.showSumina
                            ? "rinCardInactive" : "rinCardIcon"}
                            onClick={this.toggleRin}>
                            <p className="tagline">the silver-tongued jester</p>
                        </div>
                    </div>
                    <div className="character">
                        <p className="robynActive">ROBYN DEVOLKRYS</p>
                        <div className={this.state.showBrighte || this.state.showLaleh || this.state.showLydi || this.state.showNakoa || this.state.showRin || this.state.showSumina
                            ? "robynCardInactive" : "robynCardIcon"}
                            onClick={this.toggleRobyn}>
                            <p className="tagline">the fallen angel</p>
                        </div>
                    </div>
                    <div className="character">
                        <p className="suminaActive">SUMINA KOZEN</p>
                        <div className={this.state.showBrighte || this.state.showLaleh || this.state.showLydi || this.state.showNakoa || this.state.showRin || this.state.showRobyn
                            ? "suminaCardInactive" : "suminaCardIcon"}
                            onClick={this.toggleSumina}>
                            <p className="tagline">the master of all trades</p>
                        </div>
                    </div>
                    <br></br><br></br>
                    <div className="greySection"></div>
                    <p className="selectInstruct">
                        {this.state.showBrighte || this.state.showLaleh || this.state.showLydi || this.state.showNakoa || this.state.showRin || this.state.showRobyn || this.state.showSumina
                            ? <div><img src={arrow} className="arrow" /><p className="sArrow_instruct">scroll to view</p></div>
                            : "✦ select a character ✦"}
                    </p>
                </div>
                <br></br>
                <div className="greyDetailsSection"></div>
                {this.state.showBrighte ?
                    <div className="detail"><Brighte />
                        <p className="sArrow_instruct">click to go back to character selection</p>
                        <img src={arrow} className="upArrow" onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} />
                    </div> : null}
                {this.state.showLaleh ?
                    <div className="detail"><Laleh />
                        <p className="sArrow_instruct">click to go back to character selection</p>
                        <img src={arrow} className="upArrow" onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} />
                    </div> : null}
                {this.state.showLydi ?
                    <div className="detail"><Lydi />
                        <p className="sArrow_instruct">click to go back to character selection</p>
                        <img src={arrow} className="upArrow" onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} />
                    </div> : null}
                {this.state.showNakoa ?
                    <div className="detail"><Nakoa />
                        <p className="sArrow_instruct">click to go back to character selection</p>
                        <img src={arrow} className="upArrow" onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} />
                    </div> : null}
                {this.state.showRin ?
                    <div className="detail"><Rin />
                        <p className="sArrow_instruct">click to go back to character selection</p>
                        <img src={arrow} className="upArrow" onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} />
                    </div> : null}
                {this.state.showRobyn ?
                    <div className="detail"><Robyn />
                        <p className="sArrow_instruct">click to go back to character selection</p>
                        <img src={arrow} className="upArrow" onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} />
                    </div> : null}
                {this.state.showSumina ?
                    <div className="detail"><Sumina />
                        <p className="sArrow_instruct">click to go back to character selection</p>
                        <img src={arrow} className="upArrow" onClick={() => window.scrollTo({ top: 0, behavior: 'smooth' })} />
                    </div> : null}
                <br></br>
            </div>
        )
    }
}

export default Kingpin_Chara;